
import './pictures_page.scss'
import { useNavigate } from 'react-router-dom'


export const PicturesPage = ({langEn}) => {
  const navigate = useNavigate();
  return (
    <>
    
    <div  className='pictures__page__container'>
        {langEn ? <h1 className='music_page_title'>PICTURES</h1> : <h1 className='music_page_title'></h1>}
        {/* <button onClick={()=> navigate('/add-pictures')}> </button> */}
        <span>{langEn ? 'This section is currently being developped' : '  '}</span>
      
      </div>
    </>
  )
}